.top-menu {
  height: 110px;
  background: linear-gradient(90deg, white 0%, #dedede 100%, #707070 100%);
}
.top-menu__container {
  display: flex;
  align-items: center;
  height: 100%;
}
.top-menu .logo-mb {
  display: none;
}

.logo {
  background-image: url("/assets/iklogo-desc.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 150px;
  height: 100px;
  margin-top: 30px;
  margin-right: 200px;
  margin-left: 50px;
}

.top-menu-items {
  z-index: 10;
  position: absolute;
  left: 0px;
  top: 90px;
  flex-direction: column;
  width: 100%;
  clear: both;
  width: 100%;
  height: 0px;
  overflow: hidden;
  transition: height 0.6s ease-in-out;
}
.top-menu-items li {
  padding-left: 40px;
}
.top-menu-items li .nav-link {
  padding: 18px 20px;
  display: block;
  border-bottom: none;
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: bold;
  color: #262626;
  background-color: white;
}
.top-menu-items li .nav-link:hover {
  color: #a5a5a5;
}

.menu-btn {
  display: none;
  position: absolute;
  opacity: 0;
  top: -10000px;
  left: -10000px;
}
.menu-btn:checked ~ .top-menu-items {
  display: flex;
  height: 300px;
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(1) {
  transform: rotate(45deg) translate(2px, 8px);
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(3) {
  transform: rotate(-45deg) translate(2px, -8px);
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(2) {
  opacity: 0;
}

.hamburger-menu {
  display: block;
  margin-right: 50px;
}
.hamburger-menu span {
  width: 40px;
  height: 4px;
  display: block;
  margin-bottom: 3px;
  border-radius: 10px;
  background: #343433;
  transition: 0.6s all ease-in-out;
}
.hamburger-menu span:last-child {
  margin-bottom: 0;
}

